import React, {useEffect} from 'react';
import useFirestore from "../hooks/useFirestore";
import Grid from "@material-ui/core/Grid/Grid";
import Link from "./Link";
import analytics from "../data/analytics"
import {projectFirestore} from "../firebase/config";
// import useFirestoreAnalytics from "../hooks/useFirestoreAnalytics";

const Links = () => {
    const {docs} = useFirestore("links");
    console.log("links", docs);
    let currentURl = window.location.href;
    let parts = currentURl.split("/");
    let collection = "analytics";
    let docId = "links";
    let localProduction = currentURl.slice(7, 12);
    let currentPage = parts[parts.length - 1];

    useEffect(() => {
        const collectionRef = projectFirestore.collection(collection);
        let currentViews = 0;

        let localCollection = collectionRef.doc(localProduction).collection(currentPage).doc(docId).get().then(function (doc) {
            if (doc.exists) {
                console.log("doc:", doc.data());
                currentViews = doc.data().views;
                console.log("current views:", currentViews);
            } else {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
        }).then(() => {
            collectionRef.doc(localProduction).collection(currentPage).doc(currentPage).update({
                views: currentViews + 1
            }).then(function () {
                console.log("Document successfully updated!");
            });
        }).catch(function (error) {
            console.log("Error getting document:", error);

        });

    }, []);

    const linkComponents = docs.map(doc => (

        <Link key={doc.id} title={doc.title} type={doc.type} linkUrl={doc.linkUrl}
              embed={doc.type === "video" ? doc.embed : doc.fileUrl}/>
    ));

    return (
        <Grid container alignItems="center"
              justify="center" direction="column">
            {/*/!links!/*/}
            {docs && linkComponents}
        </Grid>

    )
};

export default Links;