import React, {useState} from 'react';
import Modal from "@material-ui/core/Modal/Modal";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Button from "@material-ui/core/Button/Button";
import {projectFirestore, timestamp} from "../firebase/config";
import AddIcon from '@material-ui/icons/Add';
import ProgressBar from "./ProgressBar";

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const useStyles = makeStyles((theme) => ({
    paper: {
        position: 'absolute',
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
    myButton: {
        backgroundColor: " #21D4FD",
        backgroundImage: "linear-gradient(19deg, #21D4FD 0%, #B721FF 100%)"
    }
}));
//
// const handleClick = (e) => {
//     if (e.target.classList.contains('backdrop'))
//         setSelectedImg(null)
// };


const NewLink = () => {
    const classes = useStyles();
    const [title, setTitle] = useState("");
    const [embed, setEmbed] = useState("");
    const [type, setType] = useState("");
    const [file, setFile] = useState(null);
    const [fileUrl, setFileUrl] = useState("");
    const [linkUrl, setLinkUrl] = useState("");
    const [open, setOpen] = useState(false);
    // getModalStyle is not a pure function, we roll the style only on the first render
    const [modalStyle] = useState(getModalStyle);
    const types = ['image/png', 'image/jpeg'];

    const handleClose = () => {
        setOpen(false);
    };
    const handleOpen = () => {
        setOpen(true);
    };

    const handleSubmit = (e) => {
        const collectionRef = projectFirestore.collection('links');
        console.log("Submit", e);
        const createdAt = timestamp();
        if (title !== "" && type === "video") {
            collectionRef.add({
                createdAt,
                type,
                title,
                linkUrl,
                embed
            });
        } else {
            collectionRef.add({
                createdAt,
                type,
                title,
                linkUrl,
                fileUrl
            });
        }

        handleClose();
    };

    const handleChange = (event) => {
        let {name, value, type} = event.target;
        if (name === "title") {
            console.log(value, name);
            setTitle(value)
        } else if (name === "embed") {
            console.log("embed", embed);
            setEmbed(value)
        } else if (name === "type") {
            setType(value)
        } else if (name === "file") {
            let selected = event.target.files[0];
            if (selected && types.includes(selected.type)) {
                setFile(selected);
            }
        }
        else {
            setLinkUrl(value)
        }
    };

    const body = (
        <div style={modalStyle} className={classes.paper}>
            <form>
                <input type="text" value={title} name="title" placeholder="Title"
                       onChange={handleChange}/>
                <br/>
                <input type="text" value={linkUrl} name="linkUrl" placeholder="LinkUrl"
                       onChange={handleChange}/>
                <h1>{title} {linkUrl}</h1>
                <select
                    value={type}
                    onChange={handleChange}
                    name="type"
                >
                    <option value={null}>Select Type</option>
                    <option value="image">Image</option>
                    <option value="video">Video</option>
                </select>
                <br/>
                {type === "video" &&
                <textarea name="embed" value={embed} placeholder="iFrame html" onChange={handleChange}/>}
                <br/>
                {type === "image" && <input type="file" name="file" onChange={handleChange}/>}
                {file && <ProgressBar file={file} setFile={setFile} setFileUrl={setFileUrl}/>}
                {fileUrl && <p>{fileUrl}</p>}
                <Button onClick={handleSubmit}>Submit</Button>
            </form>
        </div>
    );

    return (
        <div>
            <Button className={classes.myButton}
                    variant="contained" size="large" onClick={handleOpen}>
                <AddIcon/>
            </Button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                {body}
            </Modal>
        </div>
    )
}

export default NewLink;