import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import {red} from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Button from "@material-ui/core/Button/Button";
import YouTubeIcon from '@material-ui/icons/YouTube';
import green from "@material-ui/core/colors/green";
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import {projectFirestore} from "../firebase/config";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "90%",
        padding: "8px !important",
        margin: "5px",
        boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
    },
    overrides: {
        MuiCardContent: {
            root: {
                padding: 8,
                "&:last-child": {
                    paddingBottom: 0,
                },
            },
        },
    },
    youtube: {
        color: "black", fontWeight: 500, fontSize: 20
    },
    iFrameContainer: {
        position: "relative",
        width: "100%",
        paddingBottom: "56.25%",
        height: 0,
    },
    image: {
        position: "absolute",
        top: "0",
        left: "0",
        width: "100%",
        height: "100%"
    }
}));

const Link = ({title, linkUrl, embed, type}) => {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    const handleClick = (url, docId) => {
        console.log("i was clicked", docId);
        let currentURl = window.location.href;
        let parts = currentURl.split("/");
        let localProduction = currentURl.slice(7, 12) === "local" ? "local" : "production";
        let currentPage = parts[parts.length - 1];
        let collection = "analytics";

        const collectionRef = projectFirestore.collection(collection);
        let currentViews = 0;
        let flag = true;
        let localCollection = collectionRef.doc(localProduction).collection(currentPage).doc(docId).get().then(function (doc) {
            if (doc.exists) {
                console.log("doc:", doc.data());
                currentViews = doc.data().views;
                console.log("current views:", currentViews);
            } else {
                // doc.data() will be undefined in this case
                flag = false;
                console.log("No such document!");
            }
        }).then(() => {
            if (flag) {
                collectionRef.doc(localProduction).collection(currentPage).doc(docId).update({
                    views: currentViews + 1
                }).then(function () {
                    console.log("Document successfully updated!");
                });
            } else {
                collectionRef.doc(localProduction).collection(currentPage).doc(docId).set({
                    "views": 1
                }).then(function () {
                    console.log("Document successfully updated!");
                }).catch(function (error) {
                    console.log("Error getting document 2:", error);
                });
            }
        }).catch(function (error) {
            console.log("Error getting document:", error);
        });

        window.location.href = url;
    };

    console.log("Type:", type);

    return (
        <Card boxShadow={3} className={classes.root} item variant="outlined">
            <CardHeader styles={{padding: "8px !important"}}
                        title={title}
            />

            <div className={"iframe-container"}>
                {
                    type === "video" ? <div dangerouslySetInnerHTML={{__html: embed}}></div> :
                        <img className={classes.image} src={embed}/>
                }

            </div>

            <CardActions>
                <IconButton onClick={() => {
                    handleClick(linkUrl, title)
                }} aria-label="share">
                    <YouTubeIcon style={{color: red[500]}} fontSize="large"/>
                    <Typography
                        className={classes.youtube}> {type === "video" ? "Watch on YouTube" : "Check out my channel on YouTube!"} </Typography>
                </IconButton>
            </CardActions>
        </Card>

    )
}

export default Link;