import {useState, useEffect} from 'react';
import {projectFirestore} from "../firebase/config";

const useFirestore = (collection) => {
    const [docs, setDocs] = useState([]);
    console.log("in firestore");
    useEffect(() => {
        // this creates a listener for changes to the collection with then ame of the passed in collection
        // returns a function to unsub
        const unsub = projectFirestore.collection(collection)
        // order by the created date is descending order
            .orderBy('order')
            .onSnapshot((snap) => {
                let documents = [];
                snap.forEach((doc) => {
                    // push the info from the each of the items in the collection
                    documents.push({
                        ...doc.data(), id: doc.id
                    });
                });
                setDocs(documents);
            });
        // clean up function
        return () => unsub();


    }, [collection]);
    console.log("in firestore docs", docs);
    return {docs};
}

export default useFirestore;