import React, {useState} from 'react';

import Links from "./components/Links";
import Avatar from "@material-ui/core/Avatar/Avatar";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Grid from "@material-ui/core/Grid/Grid";
import NewLink from "./components/NewLink";
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import MyNav from "./components/MyNav";

const useStyles = makeStyles((theme) => ({
        larger: {
            width: 150,
            height: 150,
        },
        root: {
            padding: "20px",
        },
        mainDiv: {
            padding: "20px",
            backgroundColor: "#F4D03F",
            backgroundImage: "linear-gradient(132deg, #F4D03F 0%, #18b55f 100%)",
        },
        large: {
            width: theme.spacing(7),
            height:
                theme.spacing(7),
        },
    }))
;

function App() {
    const classes = useStyles();
    const headshot = "https://firebasestorage.googleapis.com/v0/b/eastfiregram.appspot.com/o/2020-headshot.jpg?alt=media&token=3fae1710-d48d-49db-af75-627ca49abdd7";
    const logo = "https://firebasestorage.googleapis.com/v0/b/eastfiregram.appspot.com/o/Logo%202.png?alt=media&token=f3e7e586-b9c2-408c-9825-83b75a339583";
    return (
        <Router>
            <MyNav/>
            <div className={classes.mainDiv}>
                <Grid container spacing={1} alignItems="center"
                      justify="center" direction="column">
                    <Grid item xs={12}>
                        <Link to={"/add"}>
                            <Avatar className={classes.larger}
                                    src={headshot}/>
                        </Link>
                    </Grid>
                    <Grid item xs={12}>
                        <img className={"responsive"} src={logo}/>
                    </Grid>
                    <Grid item xs={0} sm={1} md={2}/>
                    <Grid item xs={12} sm={10} md={8} container spacing={1} alignItems="center"
                          justify="center" direction="column">
                        <Switch>
                            <Route path="/" exact component={Links}/>
                            <Route path="/links" exact component={Links}/>
                            <Route path="/add" component={NewLink}/>
                        </Switch>
                    </Grid>
                    <Grid item xs={0} sm={1} md={2}/>
                </Grid>
            </div>
        </Router>
    );
}


const Home = () => (
    <div>
        <h1>Home Page </h1>
    </div>
)

export default App;

